import React, { useState, useEffect } from "react";
import Confetti from "react-dom-confetti";

const config = {
	angle: "90",
	spread: "360",
	startVelocity: "37",
	elementCount: "67",
	dragFriction: "0.05",
	duration: "1420",
	stagger: "3",
	width: "12px",
	height: "11px",
	perspective: "890px",
	colors: ["#a864fd", "#29cdff", "#78ff44", "#ff718d", "#fdff6a"],
};

export default function Timer({ closeTimer }) {
	const today = new Date();
	const tomorrow = new Date(today);
	tomorrow.setDate(tomorrow.getDate() + 1);
	const [countdownDate, setCountdownDate] = useState(tomorrow.getTime());
	const [showConfetti1, setShowConfetti1] = useState(false);
	const [showConfetti2, setShowConfetti2] = useState(false);
	const [showConfetti3, setShowConfetti3] = useState(false);
	const [showConfetti4, setShowConfetti4] = useState(false);
	const [state, setState] = useState({
		days: 0,
		hours: 0,
		minutes: 0,
		seconds: 0,
	});

	useEffect(() => {
		setInterval(() => setNewTime(), 1000);
		setTimeout(() => setShowConfetti1(true), 400);
		setTimeout(() => setShowConfetti2(true), 800);
		setTimeout(() => setShowConfetti3(true), 1600);
		setTimeout(() => setShowConfetti4(true), 2000);
	}, []);
	const setNewTime = () => {
		if (countdownDate) {
			const currentTime = new Date().getTime();

			const distanceToDate = countdownDate - currentTime;

			let days = Math.floor(distanceToDate / (1000 * 60 * 60 * 24));
			let hours = Math.floor(
				(distanceToDate % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
			);
			let minutes = Math.floor(
				(distanceToDate % (1000 * 60 * 60)) / (1000 * 60)
			);
			let seconds = Math.floor((distanceToDate % (1000 * 60)) / 1000);

			const numbersToAddZeroTo = [1, 2, 3, 4, 5, 6, 7, 8, 9];

			days = `${days}`;
			if (numbersToAddZeroTo.includes(hours)) {
				hours = `0${hours}`;
			} else if (numbersToAddZeroTo.includes(minutes)) {
				minutes = `0${minutes}`;
			} else if (numbersToAddZeroTo.includes(seconds)) {
				seconds = `0${seconds}`;
			}

			setState({ days: days, hours: hours, minutes, seconds });
		}
	};
	return (
		<div className="stickyTimerWrap">
			<span className="confetti1">
				<Confetti active={showConfetti1} config={config} />
			</span>
			<span className="confetti2">
				<Confetti active={showConfetti2} config={config} />
			</span>
			<span className="confetti3">
				<Confetti active={showConfetti3} config={config} />
			</span>
			<span className="confetti4">
				<Confetti active={showConfetti4} config={config} />
			</span>
			<button type="button" className="closeAct" onClick={closeTimer}>
				<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
					<path d="M 5.9902344 4.9902344 A 1.0001 1.0001 0 0 0 5.2929688 6.7070312 L 10.585938 12 L 5.2929688 17.292969 A 1.0001 1.0001 0 1 0 6.7070312 18.707031 L 12 13.414062 L 17.292969 18.707031 A 1.0001 1.0001 0 1 0 18.707031 17.292969 L 13.414062 12 L 18.707031 6.7070312 A 1.0001 1.0001 0 0 0 17.980469 4.9902344 A 1.0001 1.0001 0 0 0 17.292969 5.2929688 L 12 10.585938 L 6.7070312 5.2929688 A 1.0001 1.0001 0 0 0 5.9902344 4.9902344 z" />
				</svg>
			</button>
			<div className="stickyTimer">
				<div className="infoWrap">
					<div>Diwali Season Sale!</div>
					<span>FLAT 50% OFF</span>
				</div>
				<div className="timerWrap">
					<div>
						<div className="time">0{state.days || "0"}</div>
						<span>Days</span>
					</div>
					<div>
						<div className="time">{state.hours || "00"}</div>
						<span>Hours</span>
					</div>
					<div>
						<div className="time">{state.minutes || "00"}</div>
						<span>Minutes</span>
					</div>
					<div>
						<div className="time">{state.seconds || "00"}</div>
						<span>Seconds</span>
					</div>
				</div>
			</div>
		</div>
	);
}
